@import '../../index.scss';

.carousel__container {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.carousel__wrapper {
  display: flex;
  width: 100%;
  position: relative;
}

.carousel__content-wrapper {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.carousel__content {
  display: flex;
  transition: all 250ms linear;
  -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
  scrollbar-width: none;  /* hide scrollbar in Firefox */
}

/* hide scrollbar in webkit browser */
.carousel__content::-webkit-scrollbar, .carousel__content::-webkit-scrollbar {
  display: none;
}

.carousel__content > * {
  width: 100%;
  flex-shrink: 0;
  flex-grow: 1;
}

.carousel__content.show-2 > * {
  width: 50%;
}

.carousel__content.show-3 > * {
  width: calc(100% / 3);
}

.carousel__content.show-4 > * {
  width: calc(100% / 4);
}

.carousel__left, .carousel__right {
  position: absolute;
  font-size: 4rem;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  cursor: pointer;
}

.carousel__left {
  left: 24px;
}

.carousel__right {
  right: 24px;
}
